import React, { useEffect, useState } from 'react'
import { Alert, Box, Button, Grid, Modal, Snackbar, Typography } from '@mui/material'
import CustomCheckBox from '../CustomCheckBox'
import './Common.css'
import { getClientDetails, updateLicense } from 'app/services/clientService'
import { editDevice } from 'app/services/deviceService'
import moment from 'moment'
import { updateSessionStatus } from 'app/services/counselorService'
import { LoadingButton } from '@mui/lab'

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '70%',
    bgcolor: '#fafafa',
    border: '2px solid #fff',
    boxShadow: 24,
    borderRadius: '15px',
}

const returnDevice = [
    { name: 'Check for Physical Damage' },
    { name: 'Power on, check screen' },
    { name: 'Plug into Power to charge' },
    { name: 'Connect to Center WiFi' },
    { name: 'Fully Charge Unit' },
]

export default function ReturnDevice(props) {
    const [returnCheck, setReturnCheck] = useState([])
    const [client, setClient] = useState([])
    const [open,setOpen] = useState(false)
    const [message,setMessage] = useState("")
    const [loading,setLoading] = useState(false)
    // const [error, setError] = useState(false)

    const handleChange = (event) => {
        let checks = []
        if (event.target.checked) {
            checks = [...returnCheck, event.target.value]
        } else {
            checks = returnCheck.filter((item) => item !== event.target.value)
        }
        setReturnCheck(checks)
    }

    const handleSave = () => {
        setLoading(true)
        const sessions = client.sessions || []
        for(let i = 0 ;i<sessions.length;i++){
            if( moment(sessions[i].scheduled_at) > moment()){
                const options = {
                    data: {
                        status: 'cancelled',
                    }
                } 
                updateSessionStatus(sessions[i].id,options)
                .catch(err => {
                    setOpen(true)
                    setMessage("Something went wrong")
                })
            }
        }
        const options = {
            data: {
                is_assigned: false,
                status: 'Available',
                licens: null,
            },
        }

        const licenseOptions = {
            data: {
                status: 'Expired',
                client: null,
                action: 'Return',
                device_usage_id: props?.device_usage?.id
            },
        }

        console.log('options-->', licenseOptions);
        
        updateLicense(props.currentDevice.license.id, licenseOptions)
            .then((resp) => {
                if (resp) {
                    editDevice(options, props.currentDevice.id).then((resp) => {
                        setLoading(false)
                        props.handleReturnSuccess()
                    })
                }
            })
            .catch((err) => {
                console.log('error', err)
            })
    }
    const fetchDetails = (id) => {
        getClientDetails(id)
        .then(resp => {
            setClient(resp)
        })
        .catch(err => {
            console.log("error",err)
        })
    }
    
    useEffect(() => {
        
        if(props.currentDevice && props.currentDevice.license && props.currentDevice.license.client){
        fetchDetails(props.currentDevice.license.client.id)
        }
    }, [props.currentDevice]);


    return (
        <div>
            <Modal
                keepMounted
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    {props.type === 'return' ? (
                        <Grid container className="user-container">
                            <Grid className="return-device">
                                <h2>Return device /Expire license</h2>
                                <CustomCheckBox
                                    loading={loading}
                                    checkBoxList={returnDevice}
                                    returnCheck={returnCheck}
                                    handleChange={handleChange}
                                    handleSave={handleSave}
                                    handleCancel={props.handleCancel}
                                />
                            </Grid>
                        </Grid>
                    ) : (
                        <Grid container className="expire-container">
                            <Grid className="return-device">
                                <Typography
                                    id="keep-mounted-modal-title"
                                    variant="h6"
                                    component="h2"
                                    sx={{ color: '#f4d804' }}
                                >
                                    Are you sure?
                                </Typography>
                                <Typography
                                    id="keep-mounted-modal-description"
                                    sx={{ mt: 2, color: '#ff3d3d' }}
                                >
                                    If you click{' '}
                                    <strong>
                                        <b>Accept</b>
                                    </strong>
                                    , the license will get expired.
                                </Typography>
                                <Box
                                    sx={{
                                        display: 'table',
                                        marginLeft: 'auto',
                                        marginRight: '0px',
                                    }}
                                >
                                    <Button
                                        onClick={props.handleCancel}
                                        variant="contained"
                                        color="error"
                                    >
                                        Cancel
                                    </Button>
                                    {/* <Button
                                    onClick={handleSave}
                                    variant="contained"
                                    sx={{ ml: 2 }}
                                >
                                    Accept
                                </Button> */}
                                    <LoadingButton
                                        size="medium"
                                        sx={{ ml: 2 }}
                                        loading={props.loading}
                                        variant="contained"
                                        color="primary"
                                        onClick={handleSave}
                                    >
                                        Accept
                                    </LoadingButton>
                                </Box>
                            </Grid>
                        </Grid>
                    )}
                </Box>
            </Modal>
            <Snackbar
                open={open}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                    setOpen(false)
                }}
            >
                <Alert
                    onClose={() => {
                        setOpen(false)
                    }}
                    variant="filled"
                    severity="error"
                    sx={{ width: '100%' }}
                >
                    {message}
                </Alert>
            </Snackbar>
        </div>
    )
}
