import React from 'react';
import { Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import './DevicesList.css';

const AddDeviceBtn = () => {
    const navigate = useNavigate();

    return (
        <>
            <Button
                variant="contained"
                size="small"
                onClick={() => navigate('/devices/add')}
            >
                Add Device
            </Button>
        </>
    )
}

export default AddDeviceBtn;