import React from 'react';
import {
    Grid,
    Box,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Button
} from '@mui/material'
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import SearchAutoComplete from 'app/components/SearchAutoComplete/SearchAutoComplete';

const SearchFilter = ({ data, searchValue, handleSearch, multipleFilters, filterValue, handleFilter, autoCompleteValue, handleAutoComplete, options, selectValue, handleSelect, handleReset }) => {
    
    const renderContent = (el) => {
        let content = null;
        if(el.type === 'search') {
            content = (
                <FormControl fullWidth>
                    <TextField
                        autoFocus
                        label={el.label}
                        variant="outlined"
                        onChange={handleSearch}
                        value={searchValue}
                        name={el.name}
                        placeholder={el.placeholder}
                        type="text"
                        fullWidth
                        InputProps={{
                            startAdornment: <SearchIcon />,
                        }}
                    />
                </FormControl>
            );
        } else if(el.type === 'filter') {
            content = (
                <Box>
                    <FormControl fullWidth>
                        <InputLabel sx={{ backgroundColor: '#fafafa', padding: '0.25rem'}}>{el.label}</InputLabel>
                        <Select
                            id="demo-simple-select"
                            value={multipleFilters ? filterValue[el.name] : filterValue}
                            label={el.label}
                            name={el.name}
                            onChange={handleFilter}
                        >
                            {el.items.map((item, k) => (
                                <MenuItem key={k} value={item.value}>{item.name}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                </Box>
            )
        } else if(el.type === 'searchAutoComplete') {
            content = (
                <SearchAutoComplete
                    name={el.name}
                    label={el.label}
                    value={autoCompleteValue}
                    onChange={(val) => handleAutoComplete(val)}
                    options={options}
                    selectValue={selectValue}
                    handleSelect={(val) => handleSelect(val)}
                />
            );
        }

        return content;
    }
    
    return (
        <>
        <Grid sx={{ marginBottom: '20px'}} container rowSpacing={3} columnSpacing={6}>
            {data.map((el, i) => (
                <Grid item key={i} lg={6} md={6} sm={12} xs={12}>
                    {renderContent(el)}
                </Grid>
            ))}
        </Grid>
        <FormControl sx={{ marginBottom: '20px', display: 'flex', width: '100%' }}>
            <Button
                color="primary"
                variant='contained'
                onClick={handleReset}
                sx={{ alignSelf: 'flex-end'}}
            >
                Reset
            </Button>
        </FormControl>
        </>
    );
}

export default SearchFilter;