import { LoadingButton } from '@mui/lab'
import {
    Button,
    Checkbox,
    FormControlLabel,
    FormGroup,
    Grid,
} from '@mui/material'
import React from 'react'

const CustomCheckBox = (props) => {
    return (
        <FormGroup onSubmit={props.handleSave} onChange={props.handleChange}>
            <Grid
                container
                spacing={{ xs: 1, md: 1 }}
                columns={{ xs: 2, sm: 8, md: 12 }}
            >
                {props.checkBoxList &&
                    props.checkBoxList.map((item, i) => (
                        <Grid item xs={2} sm={4} md={4} key={i}>
                            <FormControlLabel
                                control={<Checkbox />}
                                label={item.name}
                                value={item.name}
                                checked={
                                    props.returnCheck.indexOf(item.name) === -1
                                        ? false
                                        : true
                                }
                            />
                        </Grid>
                    ))}
            </Grid>
            {props.error && (
                <span style={{ color: 'red' }}>*Select at least one</span>
            )}
            <div className="client-buttons" style={{ marginLeft: 'auto' }}>
                <Button
                    variant="contained"
                    color="error"
                    onClick={props.handleCancel}
                >
                    Cancel
                </Button>
                {/* <Button
                    sx={{ ml: 2 }}
                    variant="contained"
                    color="primary"
                    onClick={props.handleSave}
                >
                    Accept
                </Button> */}
                <LoadingButton
                    size="medium"
                    sx={{ ml: 2 }}
                    loading={props.loading}
                    variant="contained"
                    color="primary"
                    onClick={props.handleSave}
                >
                    Accept
                </LoadingButton>
            </div>
        </FormGroup>
    )
}

export default CustomCheckBox
