import React, { useState, useEffect } from 'react';
import UsersCustomTable from 'app/components/UsersCustomTable/UsersCustomTable';
import {
    Grid,
    Box,
    Snackbar,
    Alert,
} from '@mui/material';
import {
    fetchDevices,
    findDevices,
    getDeviceById
} from 'app/services/deviceService';
import { useNavigate } from 'react-router-dom';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import { tabsData } from 'app/constants/data';
import useAuth from 'app/hooks/useAuth';
import { Breadcrumb } from 'app/components';
import SearchFilter from 'app/components/SearchFilter/SearchFilter';
import AssignDeviceModal from './AssignDeviceModal';
import AddDeviceBtn from './AddDeviceBtn';
import { fetchSearchCenter, fetchDefaultCenters } from 'app/services/userService';
import './DevicesList.css';
import ReturnDevice from 'app/components/AssignDialog/ReturnDevice';
import { useDispatch } from 'react-redux';
import { resetClient } from 'app/redux/actions/ClientIntakeActions';
import SessionModal from 'app/components/SessionModal/SessionModal';

const setFilters = (user) => {
    let arr = [];
    if(user && user.role && user.role.type === 'admin'){
        arr = tabsData['deviceListing']['admin']['managed'];
    } else if(user && user.role && user.role.type === 'center') {
        arr = tabsData['deviceListing']['center']['bmc'];
    }
    return arr;
}

function setFiltersType(user, tab) {
    let type = null;
    if(user && user.role && user.role.type === 'admin'){
        if (tab == 1) {
            type = {
                status: 'All',
                device_type: 'All'
            }
        } else if (tab == 2) {
            type = {
                status: 'All'
            }
        }
    } else if (user && user.role && user.role.type === 'center') {
        if(tab == 1) {
            type = {
                status: 'All',
                device_type: 'All'
            }
        } else if (tab == 2) {
            type = {
                status: 'All'
            }
        }
    }
    return type;
}


const DevicesList = () => {
    const { user } = useAuth();
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [loading, setLoading] = useState(false);
    const [deviceData, setDeviceData] = useState([]);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);
    const [pageDetails, setPageDetails] = useState({});
    const [searchData, setSearchData] = useState("");
    const [type, setType] = useState(() => setFiltersType(user, 1));
    const [searchResult, setSearchResult] = useState(false);
    const [filterResult, setFilterResult] = useState(false);
    const [currentTab, setCurrentTab] = useState('1');
    const [deviceID, setDeviceID] = useState(null);
    const [modalVisible, setModalVisible] = useState(false);
    const [inputValue, setInputValue] = useState('');
    const [centerValues, setCenterValues] = useState([]);
    const [selectValue, setSelectValue] = useState(null);
    const [searchFilters, setSearchFilters] = useState(() => setFilters(user));
    const [open,setOpen] = useState(false)
    const [currentDevice,setCurrentDevice] = useState({})
    const [message,setMessage] = useState("")
    const [success,setSuccess] = useState(false)
    const [returnType,setReturnType] = useState("")
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState('');

    const page = 1;
    let tab = [];
    if(user && user.role){
        if(user.role.type === 'admin'){
            tab = tabsData.device['admin'];
        } else if (user.role.type === 'center') {
            tab = tabsData.device['center'];
        }
    }

    useEffect(() => {
        dispatch(resetClient())
        getAllDevices(page, currentTab);
        if(user && user.role && user.role.type === 'admin') {
            getDefaultCenters();
        }
    }, []);

    function getAllDevices(pageNumber, activeTab) {
        setLoading(true);
        fetchDevices(pageNumber, activeTab)
        .then(resp => {
            setLoading(false);
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                setDeviceData(formatDevice(resp.data, activeTab));
                setPageDetails(getMetaData(resp.meta));
            }
        }).catch(err => {
            setLoading(false);
        })
    }

    function sortDevices(search, type, pageNo, activeTab, centerName) {
        setLoading(true);
        let options = {
            search,
            type,
            page: pageNo,
            activeTab,
            centerName: (centerName ? centerName : null)
        };

        findDevices(options)
        .then((resp) => {
            setLoading(false);
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                setDeviceData(formatDevice(resp.data, currentTab));
                setPageDetails(getMetaData(resp.meta));
            }
        })
        .catch((err) => {
            console.log("error",err);
        })
    }
    
    const handleTab = (event, newValue) => {
        setCurrentTab(newValue);
        let arr = [];
        let type = null;
        if(user && user.role && user.role.type === 'admin') {
            if(newValue == 1) {
                arr = tabsData['deviceListing']['admin']['managed'];
                type = setFiltersType(user, 1);
            } else if(newValue == 2) {
                arr = tabsData['deviceListing']['admin']['unmanaged'];
                type = setFiltersType(user, 2);
            }
        } else if (user && user.role && user.role.type === 'center') {
            if(newValue == 1) {
                arr = tabsData['deviceListing']['center']['bmc'];
                type = setFiltersType(user, 1);
            } else if(newValue == 2) {
                arr = tabsData['deviceListing']['center']['centerOrClient'];
                type = setFiltersType(user, 2);
            }
        }
        setSearchFilters(arr);
        setFiltersType(type);
        getAllDevices('1', newValue);
    };

    function getMetaData(data) {
        return data.pagination
    }

    function getPassPhrase(item) {
        return (
            (item.attributes?.license?.data?.attributes?.passphrase?.data?.attributes?.name) ||
            '-'
        )
    }

    function getDeviceLicenseStatus(item) {
        let status = '';
        if(
            item && 
            item['attributes'] && 
            item['attributes']['license'] && 
            item['attributes']['license'] && 
            item['attributes']['license']['data'] && 
            item['attributes']['license']['data'].attributes
        ) {
            if(item.attributes?.license?.data?.attributes?.status === 'Used' || item.attributes?.license?.data?.attributes?.status === 'New'){
                status = 'Active';
            } else if (item.attributes?.license?.data?.attributes?.status === 'Expired') {
                status = 'Inactive';
            }
        }
        return status;
    }

    function formatDevice(data, activeTab) {
        let response = [];
        let meta = {};
        let isCenter = (user && user.role && user.role.type === 'center') ? true : false;
        if(activeTab == 1){  
            meta = {
                deviceId: '',
                device_id: 'Device ID',
                device_type: 'Device Type',
                device_serial: 'Device Serial',
                mac_address: 'Mac Address',
                is_assigned: 'Is Assigned',
                status: 'Status',
                device_action: 'Device Action',
                edit_action: 'Edit Action',
            }
            if(isCenter){
                meta.return = ""
                delete meta.edit_action;
                delete meta.device_action;
            }

            data.map((item) => {
                let obj = {
                    deviceId: item?.id || '',
                    device_id: item.attributes.device_id,
                    device_type: item.attributes.device_type,
                    device_serial: item.attributes.device_serial,
                    mac_address: item.attributes.mac_address,
                    is_assigned: String(item.attributes.is_assigned),
                    status: item.attributes.status,
                    device_action: {
                        deviceID: item.id,
                        isAssigned:
                            item.attributes.center &&
                            item.attributes.center.data
                                ? true
                                : false,
                        center_name:
                            (item &&
                                item.attributes &&
                                item.attributes.center &&
                                item.attributes.center.data &&
                                item.attributes.center.data &&
                                item.attributes.center.data.attributes &&
                                item.attributes.center.data.attributes.name) ||
                            '',
                        owned_by: item.attributes.owned_by,
                    },
                    edit_action: `/devices/edit?id=${item.id}`,
                }
                if(isCenter) {
                    obj.return = item?.attributes?.is_assigned? true :false;
                    delete obj.edit_action;
                    delete obj.device_action;
                }
                response.push(obj);
            })
            response["meta"] = meta;
        } else {
            meta = {
                deviceId:"",
                mac_address: 'Mac Address',
                ip_address: 'IP Address',
                passphrase: 'Passphrase',
                status: 'Status'
            }

            if(isCenter){
                meta.expire = ""
            }
    
            data.map((item) => {
                let obj = {
                    deviceId:item?.id || '',
                    mac_address: (item && item.attributes && item.attributes.mac_address) || '-',
                    ip_address: (item && item.attributes && item.attributes.ip_address) || '-',
                    passphrase: getPassPhrase(item),
                    status: getDeviceLicenseStatus(item)
                }

                
                
                if(isCenter){
                    obj.expire = item?.attributes?.is_assigned?true:false
                }

                response.push(obj);
            })
            
             
            response["meta"] = meta;
        }
        return response;
    }

    const searchDevice = (data, type, pageNumber) => {
        sortDevices(data, type, pageNumber, currentTab, selectValue && selectValue.label); 
    }

    const filterDevice = (data, search, pageNumber) => {
        sortDevices(search, data, pageNumber, currentTab, selectValue && selectValue.label);
    }

    const handleClearSearch = () => {
        if(checkFilters(type)) {
            if(selectValue && selectValue.label){
                searchDevice('', type, 1 );
            } else {
                getAllDevices(page, currentTab);
            }
        } else {
            filterDevice(type, '', 1);
        }
    }

    const handleClearType = (temp) => {
        if(searchData === ''){
            if(selectValue && selectValue.label){
                filterDevice(temp, '', 1);
            } else {
                getAllDevices(page, currentTab);
            }
        } else {
            searchDevice(searchData, temp, 1);
        }
    }

    const handleSearch = ({ target: { value }}) => {
        setSearchData(value);
        if(value && value.length > 2){
            setSearchResult(true);
            searchDevice(value, type, page);
        } else if(value === ""){
            handleClearSearch();
            setSearchResult(false);
        }
    }

    function checkFilters(temp){
        let allClear = true;
        for(const [key] of Object.entries(temp)){
            if(temp[key] !== 'All'){
                allClear = false;
            }
        }
        return allClear;
    }

    const handleFilter = ({ target: { name, value}}) => {
        let temp = {...type};
        temp[name] = value;
        setType(temp);

        let allClear = checkFilters(temp);
        if(allClear){
            setFilterResult(false);
            handleClearType(temp);
        } else {
            setFilterResult(true);
            filterDevice(temp, searchData, page);
        }
    }


    const handlePageChange = (event, newPage) => {
        if(searchResult){
            searchDevice(searchData, type, Number(newPage) + 1);
        } else if(filterResult) {
            filterDevice(type, searchData, Number(newPage)+1);
        } else {
            getAllDevices(newPage + 1, currentTab);
        }
    }

    const handleDevice = (deviceId, visible) => {
        setDeviceID(deviceId);
        setModalVisible(visible);
    }

    function getDefaultCenters(){
        fetchDefaultCenters()
        .then((res) => {
            if (res.data) {
                let data = []
                res.data.map((item) => {
                    const obj = {
                        id: item.id,
                        label: item.attributes?.name
                    }
                    data.push(obj)
                })
                setCenterValues(data.slice(0, 5));
            }

        }).catch(err => console.log(err))
    }

    const getCenterList = (data) => {
        fetchSearchCenter(data)
                .then((res) => {
                    if (res.data) {
                        let data = []
                        res.data.map((item) => {
                            const obj = {
                                id: item.id,
                                label: item.attributes?.name
                            }
                            data.push(obj)
                        })
                        setCenterValues(data)
                    }

                }).catch(err => console.log(err))
    }


    const handleSelect = (val) => {
        setSelectValue(val);
        if(val){
            sortDevices(searchData, type, page, currentTab, val.label);
        } else {
            sortDevices(searchData, type, page, currentTab);
        }
    }

    const handleAutoComplete = (value) => {
        setInputValue(value);
        if(value === ''){
            setCenterValues([]);
            setSelectValue(null);
            getDefaultCenters();
        } else {
            getCenterList(value);
        }
    }

    const handleReset = () => {
        setType(setFiltersType(user, currentTab));
        setSearchData('');
        setInputValue('');
        setSelectValue(null);
        getAllDevices(page, currentTab);
        getDefaultCenters();
    }

    const handleReturn = (row) => {
            getDeviceById(row.deviceId)
            .then(resp => {
                setReturnType("return")
                setCurrentDevice(resp)
                setOpen(true)
            })
            .catch(err => {
                console.log("error",err)
            })        
    }

    const handleExpire = (row) => {
        getDeviceById(row.deviceId)
            .then((resp) => {
                setReturnType('expire')
                setCurrentDevice(resp)
                setOpen(true)
            })
            .catch((err) => {
                console.log('error', err)
            })
    }

    const handleReturnSuccess = () => {
        if(returnType === "expire"){
            setMessage("License expired successfully")
        }else{
            setMessage("Device returned successfully")
        }
        getAllDevices(page, currentTab);
        setOpen(false)
        setSuccess(true)
    }

    const onClick = (id) => {
        if(user && user.role && user.role.type === 'center'){
            navigate(`/devices/${id}`);
        }
    }

    function getDeviceUsage(){
        let device;
        device = currentDevice?.license?.client?.device_usages.find(el => el.device_id == currentDevice?.id);
        return device;
    }

    
    return (
        <div className="DeviceList-container">
            <div className="Breadcrumb-box">
                <Breadcrumb routeSegments={[{ name: 'Devices List' }]} />
                {user && user.role && user.role.type === 'admin' && (
                    <AddDeviceBtn />
                )}
            </div>
            <TabContext value={currentTab}>
                <Box
                    sx={{
                        borderBottom: 1,
                        borderColor: 'divider',
                        marginBottom: '20px',
                    }}
                >
                    <TabList
                        onChange={handleTab}
                        aria-label="Client Details Segment"
                    >
                        {tab.map((item, index) => (
                            <Tab
                                label={item.name}
                                value={item.id.toString()}
                                key={index}
                            />
                        ))}
                    </TabList>
                </Box>
                <SearchFilter
                    data={searchFilters}
                    searchValue={searchData}
                    handleSearch={handleSearch}
                    multipleFilters={true}
                    filterValue={type}
                    handleFilter={handleFilter}
                    autoCompleteValue={inputValue}
                    handleAutoComplete={(val) => handleAutoComplete(val)}
                    options={centerValues}
                    selectValue={selectValue}
                    handleSelect={(val) => handleSelect(val)}
                    handleReset={handleReset}
                />
                <Grid className="user-list">
                    {!loading && deviceData.meta && (
                        <UsersCustomTable
                            page={page}
                            pageDetails={pageDetails}
                            handlePageChange={handlePageChange}
                            rows={deviceData}
                            view={'device'}
                            handleOpen={(id, visible) =>
                                handleDevice(id, visible)
                            }
                            handleReturn={handleReturn}
                            handleExpire={handleExpire}
                            onClick={onClick}
                        />
                    )}
                </Grid>
            </TabContext>
            <AssignDeviceModal
                open={modalVisible}
                deviceID={deviceID}
                handleClose={() => {
                    handleDevice(null, false)
                    handlePageChange(null, 0)
                }}
            />
            {
                currentDevice &&
            <ReturnDevice
                handleCancel={() => setOpen(false)}
                open={open}
                currentDevice={currentDevice}
                device_usage={getDeviceUsage()}
                handleReturnSuccess={handleReturnSuccess}
                type={returnType}
                
            />
            }

        <Snackbar
                open={success}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                   setSuccess(false)
                }}
            >
                <Alert
                    onClose={() => {
                        setSuccess(false)
                    }}
                    variant="filled"
                    severity="success"
                    sx={{ width: '100%' }}
                >
                    {message}
                </Alert>
            </Snackbar>
            <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
        </div>
    )
}

export default DevicesList;