import React, { Fragment, useEffect, useState } from 'react'
import {
    Alert,
    Grid,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Button,
    Select,
    InputLabel,
    MenuItem,
    OutlinedInput,
    FormControl,
    CircularProgress,
    Snackbar
} from '@mui/material'
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import { Span } from 'app/components/Typography';
import { useTheme } from '@mui/material/styles'; 
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import {
    getAllCenters
} from 'app/services/centerService';
import { editDevice } from 'app/services/deviceService';
import { tabsData } from 'app/constants/data';
import { ToastContainer, toast } from 'react-toastify';
import useMediaQuery from '@mui/material/useMediaQuery'; 
import 'react-toastify/dist/ReactToastify.css';
import { styled } from '@mui/system';
import './DevicesList.css';

const TextField = styled(TextValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
}))

const Container = styled('div')(({ theme }) => ({
    margin: '30px',
    [theme.breakpoints.down('sm')]: {
        margin: '16px',
    },
    '& .breadcrumb': {
        marginBottom: '30px',
        [theme.breakpoints.down('sm')]: {
            marginBottom: '16px',
        },
    },
}))

const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    top: '6px',
    left: '25px',
}))

const CancelButton = styled(Button)(() => ({
    display: 'block',
    color: '#fff',
    // marginTop: '20px',
    width: '86px',
    backgroundColor: '#e15260',
    '&:hover': {
        backgroundColor: '#cc2333',
        color: '#fff'
    }
}))


const AssignDeviceModal = (props) => {
    const navigate = useNavigate();
    const theme = useTheme(); 
    const fullScreen = useMediaQuery(theme.breakpoints.down('md')); 
    const [loading, setLoading] = useState(false);
    const [centerList, setCenterList] = useState([]);
    const [deviceID, setDeviceID] = useState(null);
    const [centerID, setCenterID] = useState("");
    const [open, setOpen] = useState(false);
    const [statusInfo, setStatusInfo] = useState({
        status: 'success',
        messages: []
    });


    useEffect(() => {
        if(props && props.open && props.deviceID) {
            setDeviceID(props.deviceID);
            getAllCenters()
            .then(resp => {
                if(resp){
                    setCenterList(resp);
                }
            })
        }
    }, [props]);

    const handleSubmit = () => {
        setLoading(true);
        let options = {
            data: {
                center: centerID
            }
        };

        editDevice(options, deviceID)
        .then(resp => {
            if(resp && resp.status === 'success') {
                setOpen(true);
                setStatusInfo({
                    ...statusInfo,
                    status: 'success',
                    messages: ['Device Assigned Successfully']
                });
            } else if(resp && resp.status === 'fail') {
                setLoading(false);
                let arr = [];
                resp.errors.forEach(el => {
                    arr.push(el.message);
                })
                setOpen(true);
                setStatusInfo({
                    ...statusInfo,
                    status: 'error',
                    messages: arr
                });
            } else {
                setLoading(false);
                setOpen(true);
                setStatusInfo({
                    ...statusInfo,
                    status: 'error',
                    messages: ['Something went wrong']
                })
            }
        })
    }

    const closeModal = () => {
        setCenterList([]);
        setDeviceID(null);
        setCenterID("");
        setLoading(false);
        props.handleClose();
    }


    return (
        <div>
            <Dialog
                open={props.open}
                fullScreen={fullScreen}
                onClose={() => closeModal()}
                style={{
                    display:'flex',
                    alignItems:'center',
                    justifyContent:'center',
                    maxHeight: '300px',
                    marginTop: '250px'
                }}
                aria-labelledby="form-dialog-title"
            >
                <DialogTitle id="form-dialog-title">Assign Device</DialogTitle>
                <DialogContent>
                    <ValidatorForm 
                        onSubmit={handleSubmit} 
                        onError={() => null}
                    >
                    <div>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="centerID"
                            name="centerID"
                            label={centerID === '' ? "Select Center*": ''}
                            select
                            value={centerID}
                            InputLabelProps={{shrink: false}}
                            onChange={(e) => setCenterID(e.target.value)}
                            fullWidth
                            validators={['required']}
                            errorMessages={['This field is required']}
                        >
                            <MenuItem value="">Select Center</MenuItem>
                            {centerList && centerList.map((item, i) => (
                            <MenuItem key={i} value={item.id}>{item.name}</MenuItem> 
                            ))}  
                        </TextField>
                        <div 
                        style={{ 
                            display: 'flex', 
                            alignItems: 'center', 
                            gap: '1rem',
                            marginTop: '15px'
                        }}>
                            <Box position="relative">
                                <Button disabled={loading} sx={{ padding: '6px 8px', width: '86px' }}color="primary" variant="contained" type="submit">
                                    Assign
                                </Button>
                                {loading && (
                                    <StyledProgress
                                        size={24}
                                        className="buttonProgress"
                                    />
                                )}
                            </Box>
                            <CancelButton onClick={() => closeModal()}>
                                Cancel
                            </CancelButton>
                        </div> 
                    </div>
                    </ValidatorForm>
                </DialogContent>
            </Dialog>
            <Snackbar
                open={open}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                    setOpen(false)
                    closeModal();
                }}
            >
                <div>
                {statusInfo && statusInfo.messages.map((el) => (    
                    <Alert
                        onClose={() => {
                            setOpen(false)
                            closeModal();
                        }}
                        variant="filled"
                        severity={statusInfo.status}
                        sx={{ width: '100%', marginBottom: '10px' }}
                    >
                        {el}
                    </Alert>
                ))}
                </div>
            </Snackbar>
        </div>
    );
}

export default AssignDeviceModal;