import { makeRequest } from './authenticationService';

export const fetchClients = (page, param) => {
    let url = `/api/clients?pagination[page]=${page}&counsellor=${param}`;

    return makeRequest('GET', url)
        .then((resp) => {
            return resp;
        })
        .then((json) => {
            return json;
        }); 
}
